import * as React from 'react';
import { Link } from 'react-router-dom';

import Staff from '@miq/staffjs';
import { useDocumentTitle, useRequest } from '@miq/hookjs';
import { Response, uuid } from '@miq/utiljs';
import { ProductPriceDisplay } from '@shopy/storejs';
import { Card, HorizontalScrollGridLayout, Loading } from '@miq/componentjs';

import { Cart } from './models';
import { TCart } from './types';
import { ProductImage } from './components';
import { CartDeleteButton } from './buttons';

export default function CartListView() {
  useDocumentTitle('Carts - Sales');
  const { res, loading, refresh, ...request } = useRequest(() => Cart().list());

  if (loading) return <Loading />;

  const r = new Response<TCart>(res);
  if (!r.isSuccess) return null;

  return (
    <Staff.View className="sales-index-view">
      {r.items?.map((i) => {
        const { customer_data = {}, items = [] } = i || {};
        const cart = Cart(i);

        return (
          <Card
            // border
            style={{ marginBottom: 12 }}
            actions={<CartDeleteButton instance={cart} onSuccess={() => refresh()} />}
            title={<div className="">{`${customer_data?.first_name} ${customer_data?.last_name}`}</div>}
            text={new Date(cart?.created).toLocaleDateString(undefined, {
              day: '2-digit',
              weekday: 'short',
              month: 'short',
              year: '2-digit',
            })}
            footer={
              i.products.length > 0 && (
                <div className="fw-bold">
                  <ProductPriceDisplay retail_price_data={i?.total || i?.subtotal} />
                </div>
              )
            }
            key={i.slug}
            className="mb-1"
          >
            <Link to={`cart/${i.slug}/`}>
              <HorizontalScrollGridLayout className="cart-items-preview" style={{ marginBottom: 0 }}>
                {items.map((i) => {
                  return (
                    <div key={uuid()}>
                      <ProductImage product={i.product} {...{ width: 48, height: 48 }} />
                    </div>
                  );
                })}
              </HorizontalScrollGridLayout>
            </Link>
          </Card>
        );
      })}
    </Staff.View>
  );
}
